import React from 'react'
import { Helmet } from 'react-helmet'

import { graphql } from 'gatsby'
import { CardColumns, Jumbotron } from 'reactstrap'

import { flattenFileNode } from '../helpers'

import Layout from '../components/layout'
import ArticlePreview from '../components/article-preview'

export default ({ data, location }) => {
  // aplat des objets
  let nodes = data.allMarkdownRemark.edges.map(flattenFileNode)

  // On les classe par date décroissante
  nodes.sort((a, b) => {
    const dA = new Date(a.modifiedTime)
    const dB = new Date(b.modifiedTime)
    return dB - dA
  })
  nodes = nodes.slice(0, 6)

  return (
    <Layout location={location} data={data}>
      <Helmet>
        <meta name="robots" content="index, follow" />
        <meta name="msvalidate.01" content="23C4486B3F6CCCF85A73AA582BA8DF9E" />
        <meta
          name="keywords"
          content="cours, lycée, physique, chimie, informatique, isn, nsi, lyceum"
        />
        <meta
          name="description"
          content="Un site open-source pour les lycéens."
        />
        <meta
          name="classification"
          content="lycée cours physique chimie informatique"
        />
        <meta name="author" content="Benjamin Abel" />
        <link rel="canonical" href="https://lyceum.fr" />
      </Helmet>
      <Jumbotron>
        <h1>Derniers cours</h1>
      </Jumbotron>
      <CardColumns>
        {nodes.map((node) => (
          <ArticlePreview
            key={node.id}
            node={node}
            dataShowClasse
            dataShowMatter
            dataShowPart
            dataShowModifiedTime
          />
        ))}
      </CardColumns>
    </Layout>
  )
}

export const query = graphql`
  query HomeQuery {
    __typename
    allMarkdownRemark(filter: { fields: { type: { eq: "course" } } }) {
      edges {
        node {
          id
          parent {
            id
            ... on File {
              id
              name
              modifiedTime
            }
          }
          frontmatter {
            tags
          }
          fields {
            slug
            classe
            matter
            nPart
            part
            nChapter
            chapter
          }
        }
      }
    }
  }
`
